  text-align: center;
  height: 33px;
  width: 33px;
  line-height: 36px;
}

.ui-datepicker-calendar tbody td.ui-datepicker-today {
  background-color: #284B72;
  color: white;
}

.ui-datepicker-calendar tbody td.ui-datepicker-other-month {
