.side_nav a{
  color: #fff !important;
  background-color: #457;
}

.toolbar-header .active {
  background-color: #333;
  color: #fff;
}
.toolbar-header button:active {background-color: #999;}
.toolbar-header button {
  background: white;
  border: 1px solid #333;
  color: #333;
  padding: 5px 10px;
  margin: 5px;
  height: auto;
  text-transform: none;
}

.wrapper{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: 
    "top_nav top_nav top_nav top_nav"
    "side_nav m-content m-content m-content";
}

.top_nav{
  grid-area: top_nav; 
  height: 21px;
}
.side_nav{grid-area: side_nav;
  position: relative;
  z-index: 100;
}
.m-content{grid-area: m-content;}

.toolbar-body img{margin: 5px;}